.work {
    margin-right: 10px;
    width: 350px;

    display: flex;
    position: relative;
}

.work_background {
    padding: 15px;
    margin-bottom: 45px;
    width: 100%;

    display: block;

    background-color: #161616;
    position: relative;
}

.work_background:hover {
    transform: translate(0px, -20px);
    box-shadow: -10px -10px 0px 0px rgba(221, 45, 100, 0.4);
    background-image: -webkit-linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);
    background-image: linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);
    transition-timing-function: cubic-bezier(.25, .25, .75, .75);
    transition: all 0.5s ease-in-out 0s;
}

.work_background:before {
    content: " ";

    height: 200px;
    width: 200px;

    position: absolute;
    top: -10px;
    left: -10px;

    background-image: -webkit-linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);
    background-image: linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);

    z-index: -2;
}

.work_background:hover:before {
    height: 100%;
    width: 100%;

    opacity: 0;
    transition-timing-function: cubic-bezier(.25, .25, .75, .75);
    transition-duration: 0.4s;
    transition-property: opacity, transform;
    transition: all 0.5s ease-in-out 0s;
}


.work_background a {
    text-decoration: none;
}


.work_info_block {
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
}

.work_img {
    width: 100%;
    height: 200px;

    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;

    position: relative;

    overflow: hidden;
}

.work_img img {
    width: 350px;
    min-height: 100%;

    position: absolute;

    flex-shrink: 0;
    z-index: 3;
    object-fit: cover;
}

.work_info_block h3,
.work_info_block p {
    padding: 10px;
    margin: 0 auto;

    width: 100%;

    color: #8e8e8e;
}

.work_info_block h3 {
    color: white;
}


/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .work {
        margin-bottom: 20px;
        width: 100%;
    }

    .work div {
        width: 100%;
    }

    .work_background {
        width: 100%;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .work {
        margin-right: 15px;
    }
}
